body {
  background-color: #caf0bc;
  padding: 20px;
}

.characters-list {
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 40px;
  list-style: none;
  display: flex;
}

.characters-list .item {
  background-color: #f1f193;
  border: 1px solid #9acd32;
  border-radius: 4px;
  flex-direction: column;
  flex-basis: calc(25% - 45px);
  align-items: center;
  padding: 10px;
  display: flex;
}

.characters-list .link {
  color: red;
  text-decoration: none;
}

.load-btn {
  color: #ff0;
  cursor: pointer;
  background-color: #add8e6;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  font-size: 18px;
  transition: background-color .5s, color .5s;
  display: none;
}

.load-btn:hover, .load-btn:focus {
  color: #add8e6;
  background-color: #ff0;
}

